import React from 'react';
import styles from './Row.module.css';
import { Styleable, styleAs } from '../utility/style';
export { styles as rowStyles };

export interface RowProps extends Styleable {
  spaceEven?: boolean;
}

export const Row: React.FC<RowProps> = (props) => {
  const { children, spaceEven } = props;

  const extraStyle = [];
  if (spaceEven) {
    extraStyle.push(styles.spaceEven);
  }

  return (
    <div className={styleAs(styles.row, extraStyle, props)}>
      {children}
    </div>
  );
};