import React from 'react';
import graphics from './Contact.svg';
import { SvgSymbol } from '../SvgSymbol';
import { Mark } from '../../ui/basic/Mark';

const symbol = <SvgSymbol src={graphics} alt='contact icon' />;

export interface ContactMarkProps {
}

export const ContactMark: React.FC<ContactMarkProps> = (props) => {
  return (
    <Mark symbol={symbol} name='Contact' linkTo='/contact' />
  );
};