import React from 'react';
import { Mark } from '../../ui/basic/Mark';
import graphics from './Profiles.svg';
import { SvgSymbol } from '../SvgSymbol';

const symbol = <SvgSymbol src={graphics} alt='profiles icon' />;

export interface ProfilesMarkProps {
}

export const ProfilesMark: React.FC<ProfilesMarkProps> = (props) => {
  return (
    <Mark symbol={symbol} name='Profiles' linkTo='/profiles' />
  );
};