import React from 'react';
import styles from './Alert.module.css';
import { styleAs } from '../utility/style';

export enum AlertType {
  basic = 'basic',
  info = 'info',
  positive = 'positive',
  warn = 'warn',
  negative = 'negative',
}

export interface AlertProps {
  alertType?: AlertType;
}

export const Alert: React.FC<AlertProps> = (props) => {
  const { children, alertType = 'basic' } = props;
  return (
    <div className={styleAs(styles.alert, styles[alertType])}>
      {children}
    </div>
  );
};