import React from 'react';
import styles from './basic.module.css';
import { styleAs, Styleable } from '../../utility/style';
import { useId } from '../../hooks/useId';

type HTMLButtonProps = React.DetailedHTMLProps<React.ButtonHTMLAttributes<HTMLButtonElement>, HTMLButtonElement>;
export interface ButtonProps extends Styleable, Omit<HTMLButtonProps, 'action'> {
  action?: (id: string) => void;

  wide?: boolean;
}

export const Button: React.FC<ButtonProps> = (props) => {
  const { action, children, wide, ...rest } = props;
  const id = useId();

  const extras = [];
  if (wide) {
    extras.push(styles.wide);
  }

  return (
    <>
      <button
        {...rest}
        onClick={() => action?.(id)}
        className={styleAs(styles.button, props, extras)}
      >
        {children}
      </button>
    </>
  );
};